import { useEffect, useState } from 'react';
import { Divider, Typography, Box, Button } from '@mui/material';
import FormProfile from 'src/views/common/Profile/FormProfile';
import { useDispatch } from 'src/store';
import { Redirect, useLocation } from 'react-router-dom';
import API from 'src/utils/api';
import useAuth from 'src/hooks/useAuth';
import ProfileLayout from 'src/views/common/ProfileLayout'

const style = {
  verifyText: {
    color: '#22C55E'
  },
  fontWeightBold: {
    fontWeight: 'bold'
  },
  backgroundWhite: {
    backgroundColor: '#fff'
  },
  errorInfo: {
    backgroundColor: '#168bc226',
    borderRadius: '5px'
  },
  container: {
    width: '100%',
    maxWidth: 360,
    bgcolor: 'background.paper'
  }
};

const Profile = () => {
  const [profile, setProfile] = useState();
  const location = useLocation();
  const dispatch = useDispatch();

  useEffect(() => {
    try {
      async function getProfile() {
        const response = await API.get('user/me');
        setProfile(response.data.data);
      }
      getProfile();
    } catch (err) {
      console.log('Load profile error', err)
    }
  }, []);

  const { isAuthenticated } = useAuth();
  if (isAuthenticated !== null && isAuthenticated === false) {
    const system = location.pathname.split('/')[1];
    const returnUrl = new URLSearchParams(location.search).get('returnUrl');
    return returnUrl ? <Redirect to={returnUrl} /> : <Redirect to={`/${system}/login?returnUrl=/${system}/profile`} />;
  }

  return (
    <ProfileLayout>
      <Typography variant="h6" component="b">
        Personal Information
      </Typography>
      <Divider />
      <Box sx={{ display: 'flex', alignItems: 'center' }} style={style.errorInfo} px={2} py={1} my={2}>
        <Box sx={{ flexGrow: '1' }}>
          <Typography variant="body2" component="div">
            You need to perform identity verification
          </Typography>
        </Box>
        <Box ml={1}>
          <Button variant="contained" disableElevation>
            Verify now
          </Button>
        </Box>
      </Box>
      <FormProfile profile={profile} />
    </ProfileLayout>
  );
};

export default Profile;
