import {useState} from 'react';
import { TextField, Box } from '@mui/material';
import FormControlWrapper from 'src/components/FormControlWrapper';
import LoadingButton from '@mui/lab/LoadingButton';
// import DatePicker from '@mui/lab/DatePicker';
import FormLabel from 'src/components/FormLabel';
import * as Yup from 'yup';
import { Formik } from 'formik';
import API from 'src/utils/api';
import Alert from 'src/components/Alert'

const FormProfile = ({ profile }) => {
  const [open, setOpen] = useState(false);
  const [alertError, setAlertError] = useState(false);
  const [alertMessage, setAlertMessage] = useState('');

  return (
    <>
      <Formik
        enableReinitialize
        initialValues={{
          first_name: profile ? profile.first_name : '',
          last_name: profile ? profile.last_name : '',
          email: profile ? profile.email : '',
          phone: profile ? profile.phone : '',
          country: profile ? profile.country : '',
          address: profile ? profile.address : ''
        }}
        validationSchema={Yup.object().shape({
          first_name: Yup.string().required('First name is required'),
          last_name: Yup.string().required('Last name is required'),
          email: Yup.string().required('Email is required'),
          phone: Yup.string().required('Phone is required'),
          country: Yup.string().required('Country is required'),
          address: Yup.string().required('Address is required')
        })}
        onSubmit={async (values, { setSubmitting }) => {
          try {
            // values.dob = dob
            const response = await API.put('user/me', values);
            if (response && response.status === 200) {
              setOpen(true)
              setSubmitting(false);
            }
          } catch (error) {
            setAlertError(true)
            setAlertMessage('Something wrong, please try again later!')
            setOpen(true)
          }
        }}
      >
        {({ errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values, setFieldValue }) => (
          <form onSubmit={handleSubmit}>
            <Box sx={{ display: 'flex' }}>
              <FormControlWrapper>
                <FormLabel label="First name" />
                <TextField
                  fullWidth
                  size="small"
                  margin="dense"
                  name="first_name"
                  error={Boolean(touched.first_name && errors.first_name)}
                  helperText={touched.first_name && errors.first_name}
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.first_name}
                />
              </FormControlWrapper>
              <FormControlWrapper marginLeft={5}>
                <FormLabel label="Last name" />
                <TextField
                  fullWidth
                  size="small"
                  margin="dense"
                  name="last_name"
                  error={Boolean(touched.last_name && errors.last_name)}
                  helperText={touched.last_name && errors.last_name}
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.last_name}
                />
              </FormControlWrapper>
            </Box>
            <FormControlWrapper>
              <FormLabel label="Email" />
              <TextField
                fullWidth
                size="small"
                margin="dense"
                name="email"
                error={Boolean(touched.email && errors.email)}
                helperText={touched.email && errors.email}
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.email}
              />
            </FormControlWrapper>
            <FormControlWrapper>
              <FormLabel label="Mobile" />
              <TextField
                fullWidth
                size="small"
                margin="dense"
                name="phone"
                error={Boolean(touched.phone && errors.phone)}
                helperText={touched.phone && errors.phone}
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.phone}
              />
            </FormControlWrapper>
            {/* <FormControlWrapper>
              <InputLabel style={style.fontWeightBold} htmlFor="Occupation">
                Occupation
              </InputLabel>
              <TextField fullWidth error defaultValue="" size="small" margin="dense" helperText="Incorrect entry." value={profile?.occupation} />
            </FormControlWrapper> */}
            {/* <FormControlWrapper>
              <InputLabel style={style.fontWeightBold} htmlFor="Birthday">
                Birthday
              </InputLabel>
              <DatePicker
                fullWidth
                name="dob"
                error={Boolean(touched.dob && errors.dob)}
                helperText={touched.dob && errors.dob}
                onBlur={handleBlur}
                onChange={val => {
                  setFieldValue(values.dob, val);
                }}
                value={values.dob}
                renderInput={(params) => <TextField {...params} />}
              />
            </FormControlWrapper> */}
            {/* <FormControlWrapper>
              <FormLabel label="Birthday" />
              <DatePicker
                value={dob}
                onChange={(newDob) => {
                  setDob(newDob);
                }}
                renderInput={(params) => <TextField fullWidth size="small" {...params} />}
              />
              <TextField
                fullWidth
                size="small"
                margin="dense"
                name="dob"
                error={Boolean(touched.dob && errors.dob)}
                helperText={touched.dob && errors.dob}
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.dob}
              />
            </FormControlWrapper> */}
            <FormControlWrapper>
              <FormLabel label="Country" />
              <TextField
                fullWidth
                size="small"
                margin="dense"
                name="country"
                error={Boolean(touched.country && errors.country)}
                helperText={touched.country && errors.country}
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.country}
              />
            </FormControlWrapper>
            <FormControlWrapper>
              <FormLabel label="Address" />
              <TextField
                fullWidth
                size="small"
                margin="dense"
                name="address"
                error={Boolean(touched.address && errors.address)}
                helperText={touched.address && errors.address}
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.address}
              />
            </FormControlWrapper>
            <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
              <LoadingButton variant="outlined" disableElevation type="submit" loading={isSubmitting}>
                Edit
              </LoadingButton>
            </Box>
          </form>
        )}
      </Formik>
      
      <Alert open={open} setOpen={ () => {setOpen(false)}} message={alertMessage} error={alertError} />
    </>
  );
};

export default FormProfile;
