import { useEffect, useState } from 'react';
import { Container, ListItem, List, Divider, Typography, Avatar, Grid, Box, Button, Card, useTheme } from '@mui/material';
import Page from 'src/components/Page';
import { useDispatch } from 'src/store';
import LogoutIcon from '@mui/icons-material/Logout';
import PersonOutlineIcon from '@mui/icons-material/PersonOutline';
import { Link as RouterLink } from 'react-router-dom';
import API from 'src/utils/api';
import { logout } from 'src/store/slices/auth';

const style = {
  verifyText: {
    color: '#22C55E'
  },
  fontWeightBold: {
    fontWeight: 'bold'
  },
  errorInfo: {
    backgroundColor: '#168bc226',
    borderRadius: '5px'
  },
  container: {
    width: '100%',
    maxWidth: 360,
    bgcolor: 'background.paper'
  }
};

const ProfileLayout = ({children}) => {
  const theme = useTheme()
  const [profile, setProfile] = useState();
  // const location = useLocation();
  const dispatch = useDispatch();

  useEffect(() => {
    try {
      async function getProfile() {
        const response = await API.get('user/me');
        setProfile(response.data.data);
      }
      getProfile();
    } catch (err) {
      console.log('Load profile error', err)
    }
  }, []);

  // const { isAuthenticated } = useAuth();
  // if (isAuthenticated !== null && isAuthenticated === false) {
  //   const system = location.pathname.split('/')[1];
  //   const returnUrl = new URLSearchParams(location.search).get('returnUrl');
  //   return returnUrl ? <Redirect to={returnUrl} /> : <Redirect to={`/${system}/login?returnUrl=/${system}/profile`} />;
  // }

  function handleLogout() {
    dispatch(logout());
  }

  return (
    <Container maxWidth="lg">
      <Page title="User profile">
        <Grid container>
          <Grid item xs={12} md={4}>
            <Box p={2}>
              <Card p={3}>
                <Box sx={{ display: 'flex' }} p={3}>
                  <Avatar alt="Remy Sharp" src="/static/images/avatar/1.jpg" />
                  <Box ml={2}>
                    <Typography variant="subtitle1" component="b" style={style.fontWeightBold}>
                      {profile ? profile.first_name : ''} {profile ? profile.last_name : ''}
                    </Typography>
                    <Typography variant="body2" component="div" style={style.verifyText}>
                      Verify
                    </Typography>
                  </Box>
                </Box>
                <Divider />
                <Box>
                  <List component="nav">
                    <ListItem button>
                      <Button variant="text" component={RouterLink} to="/vleppo/profile" startIcon={<PersonOutlineIcon />}>
                        Profile
                      </Button>
                    </ListItem>
                    <ListItem button>
                      <Button variant="text" component={RouterLink} to="/vleppo/user-preferences" startIcon={<PersonOutlineIcon />}>
                        User Preferences
                      </Button>
                    </ListItem>

                    <ListItem button>
                      <Button variant="text" onClick={() => handleLogout()} startIcon={<LogoutIcon />}>
                        Logout
                      </Button>
                    </ListItem>
                  </List>
                </Box>
              </Card>
            </Box>
          </Grid>
          <Grid item xs={12} md={8}>
            <Card p={2}>
              <Box p={3}>
                {children}
              </Box>
            </Card>
          </Grid>
        </Grid>
      </Page>
    </Container>
  );
};

export default ProfileLayout;
